  padding: 1rem 3rem;
  text-align: center;
  margin-top: 2.5rem;
  display: inline-block;
  border: none;
}
.boton-verde:hover {
  background-color: rgb(96.7203389831, 151.5, 0);
  cursor: pointer;
}

