.dark-mode {
  background-color: rgb(25.5, 25.5, 25.5);
}
.dark-mode p, .dark-mode td {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .anuncio,
.dark-mode .resumen-vehiculo {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark-mode .anuncio .precio,
.dark-mode .resumen-vehiculo .precio {
  color: #71B100;
}
.dark-mode .anuncio .icono,
.dark-mode .resumen-vehiculo .icono {
  filter: invert(100%);
}
.dark-mode .texto-nosotros blockquote {
  color: #FFFFFF;
}
.dark-mode .formulario label,
.dark-mode .formulario legend,
.dark-mode .formulario p {
  color: #e1e1e1;
}
.dark-mode .formulario input:not([type=submit]),
.dark-mode .formulario textarea,
.dark-mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}